.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
	padding-bottom: 0;
  height: 60px;
  z-index: 1;

  /* Animation */
  transition-timing-function: ease-in;
  transition: all 0.5s;

  display: flex;
  justify-content: space-between;
}

.nav_black {
  background-color: #111;
}

.nav_logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}

.nav_avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}

.nav-title {
  font-size: 25px;
  color: red;
  font-weight: bold;
  text-decoration: none;
}
.nav-title p {
  margin: 0;
}
